body {
    margin: 0;
    padding: 0;
    font-family: sans-serif;
}

/**全局**/
.navBar{
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 100;
    background-color: #c5443e !important;
}

.whiteTopNavBar .navBar{
    background-color: white !important;
}

.appContent{
    position: relative;
    overflow: auto;
    margin-top: 45px;
    background-color: white;
    -webkit-overflow-scrolling: touch;
}

.loadingMore{
    padding: 20px 0 40px 0;
}

.appContent::-webkit-scrollbar {display:none}

.navBar .am-segment-item{
    padding: 0 8px !important;
}

.navBar .am-segment-item-selected{
    color: #333333 !important;
}

.emptyView{
    width: 100%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
}

.emptyViewImage{
    width: 201px;
    height: 165px;
}

.arrowRight{
    width: 12px;
    height: 12px;
}

input:-webkit-autofill { -webkit-box-shadow: 0 0 0px 1000px white inset !important; box-shadow: 0 0 0px 1000px white inset !important;}

::-webkit-input-placeholder { /* WebKit, Blink, Edge */
    color:#dadada;
    font-weight: 300;
}

iframe{
    position: absolute;
}


